        const getExitDirection = (curve) => {
            return curve.p2.minus(curve.p1).normalized();
        };
        const getEnterDirection = (curve) => {
            return curve.p1.minus(curve.p0).normalized();
        };
        // Prevent
        //         /
        //       / /
